<template>
  <div class="loginWrp">
    <!--  -->
    <div class="loginCard loginCard-response">
      <!--  -->
      <div @click="closeLoginCard" class="close"></div>
      <!--  -->
      <div class="loginCardTitle">
        <div @click="loginTab" :class="['titleCell', {'titleCellActive': loginTabActive}]"></div>
        <b class="titlePoint"></b>
        <div @click="registerTab" :class="['titleCell', {'titleCellActive': registerTabActive}]"></div>
      </div>
      <!--  -->
      <div class="loginCardBody">
        <!--  -->
        <div v-show="loginTabActive">
          <br>
          <input @keyup.enter="verifyIdentifyInfo" placeholder="cnodetoken" class="identityInfo" v-model.trim="accesstoken" type="password">
          <div @click="loginTips" class="loginTips">token</div>
          <div class="rememberToken">
            <input type="checkbox" id="rememberToken" v-model="storeToekn">
            <label for="rememberToken"></label>
          </div>
          <!--  -->
          <button :disabled="loading" @click="verifyIdentifyInfo" class="submitIdentifyInfo">
            <span v-if="!loading"></span>
            <span v-if="loading"><span class="fa fa-spinner fa-spin"></span> </span>
          </button>
        </div>
        <!--  -->
        <div v-show="registerTabActive">
          <span>CNode</span><br><br>
          <span>github</span><br><br>
          <a href="https://cnodejs.org/" target="_blank">https://cnodejs.org/</a>
        </div>
      </div>
    </div>
    <!--  -->
    <div @click="closeLoginCard" class="loginCardMask"></div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      loginTabActive: true,
      registerTabActive: false,
      accesstoken: '',
      loading: false,
      storeToekn: false
    }
  },
  methods: {
    loginTab: function () {
      this.registerTabActive = false
      this.loginTabActive = true
    },
    loginTips: function () {
      alert('1.CNode\n2.github\n3.\n4.token')
    },
    registerTab: function () {
      this.loginTabActive = false
      this.registerTabActive = true
    },
    closeLoginCard: function () {
      this.$store.commit('openLoginCard', false)
    },
    verifyIdentifyInfo: function () {
      if (this.accesstoken === '') {
        alert('')
      } else {
        this.loading = true
        if (this.storeToekn === true) {
          // 10(240)setCookie
          this.$commonUtil.setCookie('accesstoken', this.accesstoken, 240)
        } else {
          // tokencookie
          this.$commonUtil.setCookie('accesstoken', this.accesstoken, 0.5)
        }
        sessionStorage['isAutoLogin'] = 'false'
        this.$root.$children[0].verifyToken()
      }
    }
  },
  computed: {
    loginStatus: function () {
      return this.$store.state.loginStatus
    }
  },
  watch: {
    loginStatus: function () {
      if (this.loginStatus && sessionStorage['isAutoLogin'] === 'false') {
        alert('' + this.$store.state.loginUsername)
        this.loading = false
        this.$store.commit('openLoginCard', false)
      }
    }
  }
}
</script>

<style scoped>
.loginWrp {
  position: fixed;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  text-align: center;
  z-index: 290;
  user-select: none;
}

/*  */
.loginWrp .loginCard {
  background: #fff;
  position: absolute;
  z-index: 1;
}

/*  */
.loginWrp .loginCardMask {
  width: 100%;
  height: 100%;
  background: #ccc;
  opacity: 0.5;
}

/*  */
.loginWrp .loginCard .close {
  height: 4rem;
  width: 4rem;
  position: absolute;
  top: 0px;
  right: 0px;
  font-size: 4rem;
  color: #ddd;
  cursor: pointer;
}

/*  */
.loginWrp .loginCard .loginCardTitle {
  width: 200px;
  height: 50px;
  margin: 0 auto;
}
.loginWrp .loginCard .loginCardTitle .titleCell {
  width: 80px;
  height: 45px;
  line-height: 45px;
  border-bottom: 5px solid #fff;
  font-size: 1.2rem;
  cursor: pointer;
  float: left;
}
.loginWrp .loginCard .loginCardTitle .titleCell.titleCellActive {
  border-bottom:  5px solid #c60023;
  font-weight: bold;
  color: #c60023;
}
.loginWrp .loginCard .loginCardTitle .titlePoint {
  width: 40px;
  height: 45px;
  line-height: 45px;
  float: left;
}

/*  */
.loginWrp .loginCard .loginCardBody {
  margin-top: 30px;
}
.loginWrp .loginCard .loginCardBody .identityInfo {
  width: 100%;
  height: 30px;
  margin-top: 10px;
  border: 1px solid #999;
  border-radius: 3px;
  font-size: 1.2rem;
  text-align: center;
}
.loginWrp .loginCard .loginCardBody .loginTips {
  margin-top: 5px;
  color: #777;
  float: left;
  cursor: pointer;
}
.loginWrp .loginCard .loginCardBody .rememberToken {
  margin-top: 5px;
  float: right;
}
.loginWrp .loginCard .loginCardBody .submitIdentifyInfo {
  width: 100%;
  height: 35px;
  margin-top: 10px;
  border: none;
  border-radius: 3px;
  background: #c60023;
  color: #fff;
  font-size: 1.1rem;
  cursor: pointer;
}

/*  */
@media only screen and (max-width: 900px) {
  /*  */
  .loginWrp .loginCard.loginCard-response {
    padding: 50px 30px 0px 30px;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
  }
}
@media only screen and (min-width: 900px) {
  /*  */
  .loginWrp .loginCard.loginCard-response {
    width: 300px;
    height: 400px;
    padding: 50px;
    border-radius: 5px;
    box-shadow: 0px 0px 10px #333;
    margin-left: -200px;
    margin-top: -250px;
    left: 50%;
    top: 50%;
  }
  /* hover */
  .loginWrp .loginCard .close:hover {
    color: #c60023;
  }
  /* hover */
  .loginWrp .loginCard .loginCardTitle .titleCell:hover {
    font-weight: bold;
    color: #c60023;
  }
  /* tipshover */
  .loginWrp .loginCard .loginCardBody .loginTips:hover {
    color: #000;
    text-decoration: underline;
  }
}
</style>



